import axios from 'axios';

//  ( )         
// request interceptor
axios.interceptors.request.use(
    function(request) {
        const token = localStorage.getItem('token');
        if (token && token != 'undefined') {
            //    
            request.headers.Authorization = token;
        }
        return request;
    },
    function (error) {
        return Promise.reject(error);
    }
);


export default axios;
